export default {
	data() {
		return {
			
		}
	},
	methods: {
		clickgetUserInfo(){
			uni.getProvider({
				service:"oauth",
				success(res){//这里获取到是哪个平台
					// console.log(res)
					if (~res.provider.indexOf('weixin')){
						uni.login({
							provider:"weixin",
							success(loginRes){//这里获取到code码
								// console.log(JSON.stringify(loginRes))
								uni.getUserInfo({
									provider:"weixin",
									success(infoRes){
										console.log(infoRes) //这获取了用户的信息
										uni.switchTab({
											url:"/pages/index/index"
										})
									},
									fail(err){
										console.log(err)
									}
								})
							}
						})
					}
				}
			})
		}
	}
}
